  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.container {
  width: 1140px;
  height: 400px;
  overflow: hidden;
  display: inline-flex;
  flex-wrap: wrap;
  justify-content: space-between; }
  .container:hover .box:before {
    top: 0; }
  .container:hover .box:after {
    bottom: 0;
    -webkit-transition-delay: 0.5s;
            transition-delay: 0.5s; }
  .container .box {
    position: relative;
    width: 280px;
    height: 400px;
    background: #424242;
    -webkit-transition: 0.5s;
    transition: 0.5s;
    overflow: hidden; }
    .container .box img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover; }
    .container .box:before {
      content: '';
      position: absolute;
      top: -100%;
      left: 0;
      width: 50%;
      height: 100%;
      background: #424242;
      z-index: 1;
      -webkit-transition: 0.5s;
      transition: 0.5s; }
    .container .box:after {
      content: '';
      position: absolute;
      bottom: -100%;
      right: 0;
      width: 50%;
      height: 100%;
      background: #424242;
      z-index: 1;
      -webkit-transition: 0.5s;
      transition: 0.5s; }
    .container .box:hover:before {
      top: -100%; }
    .container .box:hover:after {
      bottom: -100%; }

.cardImage {
